module.exports = "/* Food List items */\n\n#food-item {\n  display: grid;\n  grid-gap: 5px;\n  grid-template-columns: minmax(auto, 100px) minmax(300px, 1fr);\n  grid-template-rows: minmax(auto, auto);\n}\n\n#f-item-col1 {\n  height: 100px;\n  width: 100px;\n  padding: 10px;\n  margin: 0 10px;\n}\n\n#f-item-col2 {\n  display: grid;\n  padding: 15px 50px 10px 0;\n  border-bottom: 1px solid grey;\n}\n\n#f-item-col1 > img {\n  border-radius: 8px;\n  /* background-position: center; Center the image */\n  background-size: contain;\n}\n\n.f-item {\n  padding: 0;\n  font-size: 1.5em;\n  line-height: 1;\n  display: flex;\n  justify-content: space-between;\n}\n\n.f-item > p {\n  margin: 0 0 0 0;\n  padding: 10px 0 0 0;\n}\n\n.f-item:last-child > p {\n  padding: 0;\n  color: grey;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"