module.exports = "/* Search Input */\n#search-box:focus {\n  font-weight: 600;\n}\n#search-box:focus {\n  box-shadow: none;\n}\n/* Search Icon */\n.input-group-text {\n  background-color: #ffffff;\n  border: none;\n}\n.form-control {\n  border: none;\n}\n/* Search List */\n/* ul */\n#ul-search-list {\n  background-color: #ffffff;\n}\n/* ul p */\n#ul-search-list > p {\n  margin: 0;\n  background-color: #ffffff;\n  margin: 0.8em 0 0 1em;\n  color: gray;\n  font-size: 0.8em;\n  font-weight: 500;\n}\n#ul-search-list > li:last-child > div:last-child {\n  border-bottom: none;\n}\n/* li */\n#f-search-item {\n  display: grid;\n  grid-template-columns: minmax(auto, 100px) minmax(300px, 1fr);\n  grid-template-rows: minmax(auto, auto);\n}\n/* li col1 */\n#f-search-col1 {\n  height: 70px;\n  width: 70px;\n  padding: 10px;\n  margin: 5px;\n}\n/* li col1 img */\n#f-search-col1 > img {\n  border-radius: 8px;\n  /* background-position: center; Center the image */\n  background-size: cover;\n}\n/* li col2 */\n.f-search-col2 {\n  display: flex;\n  align-items: center;\n  border-bottom: 1px solid rgba(128, 128, 128, 0.5);\n}\n/* li col2 p */\n.f-search-col2 > p {\n  margin: 0;\n  font-weight: 600;\n}\n#ul-search-list:last-child > li > div:last-child {\n  flex-direction: column;\n  justify-content: space-evenly;\n  align-items: flex-start;\n}\n.subtitle-lighten {\n  font-weight: 400;\n  color: gray;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"